import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import Project from './Project.js'

const styles = {
    paper: {
        borderWidth: 1,
        borderColor: '#F0F0F0',
        borderStyle: 'solid',
        borderRadius: 6,
        padding: 6,
        backgroundColor: 'white'
    },
    title: {
        fontFamily: "'Gentium Basic', serif",
        verticalAlign:'bottom',
        margin: 0,
        fontSize: 35,
    },
    container: {
        padding: 15,
        width: '100%',
    },
}

class Projects extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.paper}>
            <Grid container className={this.props.classes.container}>
                <Grid item align={'center'} xs={12} style={{paddingBottom: 0}}>
                    <Typography type="title" gutterBottom className={this.props.classes.title}>
                            Projects
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Project title={'Fighting Detection System'}
                             description={'I used 3D convolutional neural network and Two Stream model to build a fighting detection system.'}
                             location={'UmboCV'}
                             link={''} />

                    <Project title={'Music ChatBot'}
                             description={'Using Reinforcement Learning and Sequence-to-Sequence Model, we implemented a ChatBot for users to query music.'}
                             location={'NTU, Intelligent Conversational Bot'}
                             link={'https://yvchen.github.io/MusicBot2/'} />
                    <Project title={'Bomberman'}
                             description={'Developed an deep learning algorithm based on supervised learning and reinforcement learning(DQN) to play the game, Bomberman.'}
                             location={'NTU, Speech Processing Laboratory'}
                             link={'https://www.youtube.com/watch?v=dAczqIxOxcI&feature=youtu.be'} />
                    <Project title={'uhu: a second hand trading platform'}
                             description={'Built a second-hand trading platform, uhu, which provided services for booking, notification, filter functions among Facebook trading groups in 42 hours.'}
                             location={'NTU, HackNTU'}
                             link={'https://www.slideshare.net/secret/LhLiZV72LMNy0G/'} />
                </Grid>
            </Grid>
        </div>
    )
  }
}

export default withStyles(styles)(Projects);



