import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

const styles = theme => ({
	link: {
    	textDecoration: 'none',
    	color: 'black',
        lineHeight: '1em',
  	},
    article: {
        fontSize: 16,
        fontFamily: "'Gentium Basic', serif",
        lineHeight: '1em',
    },
    container: {
        margin: 15,
    },
    title: {
        marginTop: 5,
        marginBottom: 5,
        fontFamily: "'Gentium Basic', serif",
        fontStyle: 'bold'
    },
    date:{
        fontFamily: "'Gentium Basic', serif",
    }
});

class Blog extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.container}>
            <Typography type="title" component="h2" className={this.props.classes.title}>
                Blogs
            </Typography>
            <Typography type="subheading" component="h2" className={this.props.classes.article}>
                <a className={this.props.classes.link} href={'https://medium.com/@darrenyaoyao.huang/do-you-ever-consider-the-gender-bias-issue-in-machine-learning-f7847b3fe1aa'}>
                    Have you ever considered the gender bias issue in Machine Learning?
                </a>
            </Typography>
            <Typography type="body1" color={'secondary'} className={this.props.classes.date}>
                Dec 3, 2017
            </Typography>
        </div>
    );
  }
}

Blog.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(Blog);



