import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';

const styles = {
    title: {
        fontSize: 16,
        fontFamily: "'Gentium Basic', serif",
        width: '100%'
    },
    auther: {
        fontFamily: "'Gentium Basic', serif",
        width: '100%'
    },
	link: {
    	textDecoration: 'none',
        color: "#87CEEB",
  	},
}

class Paper extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Typography type="title" gutterBottom className={this.props.classes.title}>
                <a href={this.props.link} className={this.props.classes.link}>
                    {this.props.title}
                </a>
            </Typography>
            <Typography type="body1" gutterBottom className={this.props.classes.auther}>
                {this.props.authers +'. '+ this.props.conference}
            </Typography>
        </div>
    )
  }
}

export default withStyles(styles)(Paper);



