import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';

const styles = {
    word: {
        fontFamily: "'Gentium Basic', serif",
        width: '100%',
        fontSize: 14
    },
}

class TravelEvent extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div >
            <Grid container>
                <Grid item xs={12} style={{padding: 0}}>
                    <Typography type="body1" gutterBottom className={this.props.classes.word}>
                        {this.props.date + ' '+ this.props.event +'. '+ this.props.location}
                    </Typography>
                </Grid>
            </Grid>
        </div>
    )
  }
}

export default withStyles(styles)(TravelEvent);



