import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Avatar from 'material-ui/Avatar'
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';

const styles = {
    bigAvatar: {
        width: '80%',
        height: '80%',
        margin: '10%',
    },
    email: {
        width: '100%',
        fontSize: 15,
        fontFamily: "'Gentium Basic', serif"
    },
    material: {
        width: '100%',
        fontSize: 15,
        fontFamily: "'Gentium Basic', serif",
    },
    link: {
    	textDecoration: 'none',
    	color: '#87CEEB',
    },
    title: {
        marginTop: 10,
        marginBottom: 20,
        fontWeight: 'bold',
        fontSize: 30,
        fontFamily: "'Gentium Basic', serif"
    },
}

class Head extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Grid container>
                <Grid item xs={3} sm={12}>
                    <Avatar alt='Darren' src={process.env.PUBLIC_URL + '/images/head.jpg'}
                            className={this.props.classes.bigAvatar} />
                </Grid>
                <Grid item xs hidden={{smUp: true}}>
                    <Typography type="headline" component="h1" className={this.props.classes.title}>
                        Hi, I am Yi-Yao (Darren) Huang
                    </Typography>
                </Grid>
            </Grid>
            <Typography type="body1" component="p" className={this.props.classes.email}>
                <center>
                    darrenyaoyao.huang@gmail.com
                </center>
            </Typography>
            <Typography type="body1" component="p" className={this.props.classes.material}>
                <center>
                    <a href={'https://drive.google.com/file/d/16d9WuXeDnoCP82K7jZJKI0SO9eQi66Yr/view?usp=sharing'} className={this.props.classes.link}>
                        Curriculum Vitae
                    </a>
                </center>
            <Typography type="body1" component="p" className={this.props.classes.material}>
                <center>
                    <a href={'https://drive.google.com/file/d/1-EeQTJYz1e6kUW4nkgVjEzIkkcJ4Q-7D/view?usp=sharing'} className={this.props.classes.link}>
                        Statement of Purpose
                    </a>
                </center>
            </Typography>
            </Typography>
        </div>
    );
  }
}

Head.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(Head);



