import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import Paper from './Paper.js'

const styles = {
    paper: {
        borderWidth: 1,
        borderColor: '#F0F0F0',
        borderStyle: 'solid',
        borderRadius: 6,
        padding: 6,
        backgroundColor: 'white'
    },
    title: {
        fontFamily: "'Gentium Basic', serif",
        verticalAlign:'bottom',
        margin: 0,
        fontSize: 35,
    },
    container: {
        padding: 15,
    },
}

class Publications extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.paper}>
            <Grid container className={this.props.classes.container}>
                <Grid item xs={12} style={{paddingBottom: 0}}>
                    <Typography type="title" gutterBottom className={this.props.classes.title}>
                        <center>
                            Publications
                        </center>
                    </Typography>
                </Grid>
                <Grid item xs={12} >
                    <Paper title={'Deep Residual Learning for Weakly-Supervised Relation Extraction'}
                           authers={'YiYao Huang, Wiliiam Wang'}
                           conference={'EMNLP2017'}
                           link={'https://arxiv.org/abs/1707.08866'}/>
                    <Paper title={'Mitigating the Impact of Speech Recognition Errors on Chatbot using Sequence-to-sequence Model'}
                           authers={'Pin-Jung Chen*, I-Hung Hsu*, Yi-Yao Huang, Hung-Yi Lee(*These authors contributed equally.)'}
                           conference={'ASRU2017'}
                           link={'https://arxiv.org/pdf/1709.07862.pdf'}/>
                    <Paper title={'BackHand: Sensing Hand Gestures via Back of the Hand'}
                           authers={'Jhe-Wei Lin, Chiuan Wang, Yi Yao Huang, Kuan-Ting Chou, Hsuan-Yu Chen, Wei-Luan Tseng, Mike Y. Chen'}
                           conference={'UIST2015'}
                           link={'https://dl.acm.org/citation.cfm?id=2807462'}/>
                </Grid>
            </Grid>
        </div>
    )
  }
}

export default withStyles(styles)(Publications);



