import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import './Head.css'

const styles = {
    words: {
        textAlign: 'justify',
        fontSize: 18,
        lineHeight: '1.2em',
        fontFamily: "'Gentium Basic', serif"
    },
    link: {
    	textDecoration: 'none',
    	color: '#87CEEB',
        lineHeight: '1em',
    },
    title: {
        marginTop: 10,
        marginBottom: 20,
        fontWeight: 'bold',
        fontSize: 30,
        fontFamily: "'Gentium Basic', serif"
    },
    container: {
        padding: 15,
    },
}

class Goal extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div>
            <Grid container className={this.props.classes.container} >
                <Grid item xs={12} hidden={{smDown: true}}>
                    <Typography type="headline" component="h1" className={this.props.classes.title}>
                        Hi, I am Yi-Yao (Darren) Huang
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <Typography type="body1" component="p" className={this.props.classes.words}>
                        I am a senior undergraduate student at&nbsp;
                        <a href={'https://www.ee.ntu.edu.tw/en/'} className={this.props.classes.link}>
                        National Taiwan University EE </a> and I am working at&nbsp;
                        <a href={'https://umbocv.ai/'} className={this.props.classes.link}>UmboCV</a> as a research intern.
                        <b> I am looking for a Fall 2018 Ph.D. degree</b> in Artificial Intelligence, Natural Language Processing,
                        Cognitive Science, Computer Vision and Human-Computer Interaction.
                        <br/>
                        <br/>
                        My goal is to build an AI tutor to read books (90,000+ words), and
                        transform knowledge into articles and videos to teach children across the world.
                        In order to realize my goal, I will focus on how machine can learn new concept,
                        how machine can understand human language, and how AI can apply to education.
                    </Typography>
                </Grid>
            </Grid>
        </div>
    );
  }
}

Goal.propTypes = {
   classes: PropTypes.object
}

export default withStyles(styles)(Goal);



