import React, { Component } from 'react';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Grid from 'material-ui/Grid';
import TravelEvent from './TravelEvent.js'

const styles = {
    paper: {
        borderWidth: 1,
        borderColor: '#F0F0F0',
        borderStyle: 'solid',
        borderRadius: 6,
        padding: 6,
        backgroundColor: 'white'
    },
    title: {
        fontFamily: "'Gentium Basic', serif",
        verticalAlign:'bottom',
        margin: 0,
        fontSize: 35,
    },
    container: {
        padding: 15,
    },
}

class Travel extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div className={this.props.classes.paper}>
            <Grid container className={this.props.classes.container}>
                <Grid item xs={12} style={{paddingBottom: 0}} align={"center"} >
                    <Typography type="title" gutterBottom className={this.props.classes.title}>
                        Travel
                    </Typography>
                </Grid>
                <Grid item xs={12}>
                    <TravelEvent
                        location={'Copenhagen, Denmark'}
                        event={'EMNLP2017'}
                        date={'Sep 6-14'}/>
                    <TravelEvent
                        location={'Shanghi, China'}
                        event={'Tourism'}
                        date={'Sep 1-5'}/>
                </Grid>
            </Grid>
        </div>
    )
  }
}

export default withStyles(styles)(Travel);



